// src/hooks/useSort.js
import React, { useState, useMemo } from "react";
import { ArrowUp, ArrowDown } from "lucide-react";

// Hm sp xp t nhin (natural sort) c gi li  sp xp nhm c ch
const naturalSort = (a, b) => {
  // S dng Intl.Collator  so snh chui mt cch t nhin, coi s l s
  const collator = new Intl.Collator(undefined, { numeric: true, sensitivity: 'base' });
  return collator.compare(a, b);
};

/**
 * A custom hook for sorting arrays of objects with advanced logic.
 * @param {Array} items - The initial array of items to sort.
 * @param {Object} initialSortConfig - The initial sorting configuration.
 * @returns {Object} - An object containing sorted items and sorting utilities.
 */
export const useSort = (
  items = [],
  initialSortConfig = { key: "name", direction: "ascending" }
) => {
  const [sortConfig, setSortConfig] = useState(initialSortConfig);

  const sortedItems = useMemo(() => {
    if (!items) return [];
    let sortableItems = [...items];
    if (sortConfig.key !== null) {
      sortableItems.sort((a, b) => {
        // Hm tin ch  ly gi tr t cc key lng nhau (v d: 'details.name')
        const getNestedValue = (obj, key) =>
          key.split(".").reduce((o, i) => (o ? o[i] : undefined), obj);

        const aValue = getNestedValue(a, sortConfig.key);
        const bValue = getNestedValue(b, sortConfig.key);

        // X l trng hp gi tr l null hoc undefined  trnh li
        if (aValue === null || aValue === undefined) return 1;
        if (bValue === null || bValue === undefined) return -1;

        // --- LOGIC SP XP TY CHNH THEO YU CU ---

        // Bc 1: Phn loi gi tr (l chui thun s hay chui c ch)
        // isFinite kim tra xem mt gi tr c phi l s hu hn khng.
        // Chui "001" s c coi l s, cn "RAIL-001" th khng.
        const isNumericA = isFinite(aValue) && aValue.toString().trim() !== "";
        const isNumericB = isFinite(bValue) && bValue.toString().trim() !== "";

        // Bc 2: u tin nhm - a chui c ch ln trc chui ch c s
        if (!isNumericA && isNumericB) {
          // A l chui c ch, B l chui s -> A i trc
          return -1;
        }
        if (isNumericA && !isNumericB) {
          // A l chui s, B l chui c ch -> A i sau
          return 1;
        }

        // Bc 3: Sp xp bn trong tng nhm
        let comparison = 0;
        if (isNumericA && isNumericB) {
          // Nu c 2 u l chui s, so snh chng nh nhng con s thc s
          comparison = parseFloat(aValue) - parseFloat(bValue);
        } else {
          // Nu c 2 u l chui c ch, dng hm sp xp t nhin
          comparison = naturalSort(aValue.toString(), bValue.toString());
        }
        
        // Cui cng, p dng chiu sp xp (tng dn/gim dn)
        return comparison * (sortConfig.direction === "ascending" ? 1 : -1);
      });
    }
    return sortableItems;
  }, [items, sortConfig]);

  const requestSort = (key) => {
    let direction = "ascending";
    if (sortConfig.key === key && sortConfig.direction === "ascending") {
      direction = "descending";
    }
    setSortConfig({ key, direction });
  };

  return { items: sortedItems, requestSort, sortConfig };
};


/**
 * Component hin th tiu  bng c th sp xp.
 * @param {Array} columns - Cu hnh cc ct ca bng.
 * @param {Function} requestSort - Hm  yu cu sp xp.
 * @param {Object} sortConfig - Cu hnh sp xp hin ti.
 * @param {Function} t - Hm dch thut.
 * @returns {JSX.Element} - Component tiu  bng.
 */
export const SortableHeader = ({ columns, requestSort, sortConfig, t }) => {
  // Hm  ly biu tng sp xp (mi tn ln/xung)
  const getSortIcon = (key) => {
    if (sortConfig.key !== key) {
      return null;
    }
    if (sortConfig.direction === "ascending") {
      return <ArrowUp className="w-4 h-4 ml-1 inline-block" />;
    }
    return <ArrowDown className="w-4 h-4 ml-1 inline-block" />;
  };

  return (
    <thead className="bg-gray-50 dark:bg-gray-700/50 sticky top-0 z-10">
      <tr>
        {columns.map((col) => (
          <th
            key={col.key}
            scope="col"
            className={`px-6 py-3 font-medium text-gray-500 dark:text-gray-300 uppercase whitespace-nowrap ${
              col.className || "text-left"
            } ${
              col.sortable
                ? "cursor-pointer hover:bg-gray-100 dark:hover:bg-gray-700 select-none"
                : ""
            }`}
            onClick={() => col.sortable && requestSort(col.key)}
          >
            {t(col.label)}
            {col.sortable && getSortIcon(col.key)}
          </th>
        ))}
      </tr>
    </thead>
  );
};