// src/views/PurchasingView.js
import React from "react"; // Xa useState v khng cn selectedIds na
import { CheckCircle, RotateCcw, XCircle } from "lucide-react";
// Xa toast v khng cn thng bo li chn item na

const PurchasingView = ({ items, onUpdateStatus, onCancel, categories, t }) => {
  // --- XA B STATE V CC HM X L CHO VIC CHN NHIU MC ---
  // const [selectedIds, setSelectedIds] = useState([]);
  // ... (xa cc hm handleSelect, handleConfirmPurchase, handleSelectAll)

  const formatCurrency = (amount) => {
    if (typeof amount !== "number") return "0";
    return new Intl.NumberFormat(t("locale_string")).format(amount);
  };

  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg shadow overflow-hidden">
      <div className="p-6 border-b dark:border-gray-700">
        <h2 className="text-xl font-bold text-gray-800 dark:text-gray-100">
          {t("purchasing_list")}
        </h2>
        <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
          {t("purchasing_desc")}
        </p>
        {/* --- XA NT "XC NHN  MUA" KHI HEADER --- */}
      </div>

      {/* --- Giao din Bng cho Desktop --- */}
      <div className="overflow-x-auto hidden md:block">
        <table className="w-full text-sm">
          <thead className="bg-gray-50 dark:bg-gray-700/50">
            <tr>
              {/* --- XA CT CHECKBOX --- */}
              <th className="px-6 py-3 text-left font-medium uppercase">
                {t("device_name")}
              </th>
              <th className="px-6 py-3 text-left font-medium uppercase">
                {t("category")}
              </th>
              <th className="px-6 py-3 text-right font-medium uppercase">
                {t("purchase_quantity")}
              </th>
              <th className="px-6 py-3 text-right font-medium uppercase">
                {t("price")} (VN)
              </th>
              <th className="px-6 py-3 text-center font-medium uppercase">
                {t("actions")}
              </th>
            </tr>
          </thead>
          <tbody className="divide-y dark:divide-gray-700">
            {items && items.length > 0 ? (
              items.map((item) => (
                <tr
                  key={item.id}
                  className="hover:bg-gray-50 dark:hover:bg-gray-700/50"
                >
                  {/* --- XA CT CHECKBOX --- */}
                  <td className="px-6 py-4 font-medium">{item.name}</td>
                  <td className="px-6 py-4 capitalize">
                    {(categories.find((c) => c.id === item.category) || {})
                      .name || item.category}
                  </td>
                  <td className="px-6 py-4 text-right font-semibold">
                    {item.purchaseQuantity}
                  </td>
                  <td className="px-6 py-4 text-right">
                    {formatCurrency(item.price)}
                  </td>
                  <td className="px-6 py-4 text-center">
                    {/* --- CP NHT LI CT THAO TC --- */}
                    <div className="flex items-center justify-center space-x-2">
                      <button
                        onClick={() => onUpdateStatus([item.id])} // Gi i mng cha 1 id
                        className="p-2"
                        title={t("confirm_purchased_count", { count: 1 })}
                      >
                        <CheckCircle className="w-5 h-5 text-green-600 hover:text-green-400" />
                      </button>
                      <button
                        onClick={() => onCancel("revert-purchasing", item)}
                        className="p-2"
                        title={t("revert_to_pending")}
                      >
                        <RotateCcw className="w-5 h-5 text-yellow-600 hover:text-yellow-400" />
                      </button>
                      <button
                        onClick={() => onCancel("cancel-purchasing", item)}
                        className="p-2"
                        title={t("cancel_purchase")}
                      >
                        <XCircle className="w-5 h-5 text-red-600 hover:text-red-400" />
                      </button>
                    </div>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan="5" className="text-center py-12">
                  {t("no_data_available")}
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

      {/* --- Giao din Card cho Mobile ( cp nht) --- */}
      <div className="md:hidden p-4 space-y-4">
        {items && items.length > 0 ? (
          items.map((item) => (
            <div
              key={item.id}
              className="rounded-lg p-4 space-y-2 shadow bg-white dark:bg-gray-800"
            >
              <div className="flex items-start justify-between">
                <div>
                  <p className="font-bold text-gray-900 dark:text-gray-100">
                    {item.name}
                  </p>
                  <p className="text-sm text-gray-500 dark:text-gray-400 capitalize">
                    {(categories.find((c) => c.id === item.category) || {})
                      .name || item.category}
                  </p>
                </div>
                {/* --- Cp nht cm nt hnh ng cho mobile --- */}
                <div className="flex items-center space-x-1">
                  <button
                    onClick={() => onUpdateStatus([item.id])}
                    className="p-2"
                    title={t("confirm_purchased_count", { count: 1 })}
                  >
                    <CheckCircle className="w-5 h-5 text-green-600 hover:text-green-400" />
                  </button>
                  <button
                    onClick={() => onCancel("revert-purchasing", item)}
                    className="p-2"
                    title={t("revert_to_pending")}
                  >
                    <RotateCcw className="w-5 h-5 text-yellow-600 hover:text-yellow-400"/>
                  </button>
                  <button
                    onClick={() => onCancel("cancel-purchasing", item)}
                    className="p-2"
                    title={t("cancel_purchase")}
                  >
                    <XCircle className="w-5 h-5 text-red-600 hover:text-red-400"/>
                  </button>
                </div>
              </div>
              <div className="text-sm text-gray-700 dark:text-gray-300 grid grid-cols-2 gap-x-4 pt-2 border-t dark:border-gray-700">
                <p>
                  <strong>{t("purchase_quantity")}:</strong>{" "}
                  <span className="font-semibold">{item.purchaseQuantity}</span>
                </p>
                <p>
                  <strong>{t("price")}:</strong>{" "}
                  <span className="font-semibold">
                    {formatCurrency(item.price)} VN
                  </span>
                </p>
              </div>
            </div>
          ))
        ) : (
          <div className="text-center py-12 text-gray-500 dark:text-gray-400">
            {t("no_data_available")}
          </div>
        )}
      </div>
    </div>
  );
};

export default PurchasingView;